import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

import step1 from '@/components/signUp/step1'
import step2 from '@/components/signUp/step2'

import main from '@/components/login/main'
import deposit from '@/components/login/deposit'
import transfer from '@/components/login/transfer'
import loan from '@/components/login/loan'
import depositfinish from '@/components/login/depositFinish'
import transferfinish from '@/components/login/transferFinish'
import loanfinish from '@/components/login/loanFinish'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/signup/step1',
      name: 'step1',
      component: step1
    },
    {
      path: '/signup/step2',
      name: 'step2',
      component: step2
    },
    {
      path: '/login/main',
      name: 'main',
      component: main
    },
    {
      path: '/login/deposit',
      name: 'deposit',
      component: deposit
    },
    {
      path: '/login/transfer',
      name: 'transfer',
      component: transfer
    },
    {
      path: '/login/loan',
      name: 'loan',
      component: loan
    },
    {
      path: '/login/deposit_finish',
      name: 'deposit finish',
      component: depositfinish
    },
    {
      path: '/login/transfer_finish',
      name: 'transfer finish',
      component: transferfinish
    },
    {
      path: '/login/loan_finish',
      name: 'loan finish',
      component: loanfinish
    }
  ]
})



